import jwt from 'jsonwebtoken';

export const refreshPassword = async (token, oldPassword, newPassword) => {
    const user_name = jwt.decode(token)['sub'];
    const body = JSON.stringify({login: user_name, old_password: oldPassword, new_password: newPassword});
    const response = await fetch('http://127.0.0.1:8000/api/refresh_password', {
        method: 'POST',
        body: body
    });
    return response.json();
}
