import {ADD_PROFILE_INFO} from '../types'

const initialState = {
    user_name: null,
    create_at: null,
    add_bus_stop_time: "none",
    email_activatet: false,
    email: null
}

export const profileReducer = (state=initialState, action) => {
    switch (action.type) {
        case ADD_PROFILE_INFO:
            return ({ ...state, ...action.payload })
        default: return state
    }
}